*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  
}

.container{
  display: flex;
  align-items: center;
  justify-content: center;
}

.image-section{
  width: 480px;
  height: 100vh;
  background-image: url("../public/Assets/hide-small.png"),url("../public/Assets/biscuit.png");
  background-repeat: no-repeat;
  background-size: 130px, 45%;
  background-position: 40px 40px, top right;
  background-color: #071ffc;
}

.gift, .gifted, .btn{
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 2.5rem;
}
.gift img{
  margin-top: 7rem;
  width: 350px;
  height: auto;
}

.btn{
  position: relative;
}

.btn img{
  display: block;
 cursor: pointer;

}

.play-btn{
  position:absolute;
  font-size: 17px;
  top: 82%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #fff;
  font-weight: bold;
  pointer-events: none;
}